	export default {
		data() {
			return {
				type:0,
				channel:3,
				mobile:'',
				code:'',
				isclick:false,
				isCode:false,
				codeMsg:'获取验证码',
			}
		},
		created(){
			let _this=this;
		},
		mounted(){
			let _this=this;
		},	
		methods: {
			//登录
			login:function(){
				let _this=this;
				_this.validation1();
				if(_this.isclick){
					_this.validation2();
					if(_this.isCode){
						let data={
							type:_this.type,
							channel:_this.channel,
							mobile:_this.mobile,
							code:_this.code,
						};
						_this.$axios(_this.$baseUrl.login,data).then(res =>{
							if(res.data.status==1){
								uni.setStorage({
									key: 'token',
									data: res.data.token,
								});
								uni.showToast({
									title: res.data.msg,
									icon:'success',
									duration: 1000
								});
								uni.navigateBack();
							}else{
								uni.showToast({
									title: res.data.msg,
									icon:'none',
									duration: 1000
								});
							}
						}).catch(error =>{
							
						})
					}
				}
			},
			//验证手机号
			validation1:function(){
				let _this=this;
				if(_this.mobile == ""){
					uni.showToast({
						title: '请输入手机号',
						icon:'none',
						duration: 2000,
					});
					return;
				}else if(!(/^1[34578]\d{9}$/.test(_this.mobile))){
					uni.showToast({
						title: '手机号格式不正确',
						icon:'none',
						duration: 2000
					});
					return;
				}else{
                  _this.isclick=true;
                };
			},
			//验证手机验证码
			validation2:function(){
				let _this=this;
				if(!_this.isCode){
					uni.showToast({
						title: '请获取验证码',
						icon:'none',
						duration: 2000
					});
					return;
				}else if(_this.code==""){
					uni.showToast({
						title: '请输入验证码',
						icon:'none',
						duration: 2000
					});
					return;
				}
			},
			//获取手机验证码
			getCode:function(){
				let _this=this;
				_this.validation1();
				if(_this.isclick){
					let data={
						type:_this.type,
						mobile:_this.mobile,
					};
					_this.$axios(_this.$baseUrl.sendCode,data).then(res =>{
						if(res.data.status==1){
							_this.isclick=false;
							_this.isCode=true;
							var num = 60;
							var time = setInterval(function(){
								num--;
								_this.codeMsg = num +'s后重试';
								if(num == 0){
								  clearInterval(time);
								  _this.codeMsg = '发送验证码';
								  _this.isclick=true;
								  _this.isCode = false;
								}
							},1000)
							uni.showToast({
								title: res.data.msg,
								icon:'success',
								duration: 1000
							});
						}else{
							uni.showToast({
								title: res.data.msg,
								icon:'none',
								duration: 1000
							});
						}
					}).catch(error =>{
						
					})
				}else{
					
				}
			}
		}
	}
